.footer-section{

    width: 100%;
    height: 300px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: end;

}
.footer-bkg{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
 
    img{
        position: absolute;
        width: 100%;
        height: 100%;
    }
}
.footer-image{
    width: 20%;
    height: 20%;
    margin:10px 50px;
    position: relative;
background-color: red;
img{
    width: 100%;
    height: 100%;
}
    
}